.footer-address {
	a {
		color: $white;
		text-decoration: none;
		&:hover {
			color: $white;
			text-decoration: underline;
		}
		&.hover-reverse{
			text-decoration: underline;
			&:hover{
				text-decoration: none;
			}
		}
	}
	.footer-col {
		@include media-breakpoint-up(xl) {
			border-left: 1px solid rgba($body-bg,.2);
		}
	}
}
.bg-footer {
	background-color: $bg-footer;
}
.bg-footer-dark {
	background-color: $bg-footer-dark;
}

.bg-footer, .bg-footer-dark {
	.text-muted {
		color: rgba($white,.4) !important;
	}
	.text-primary {
		color: $footer-text-primary !important;
	}
}
.footer-logo{
	margin-top: 1.6rem;
	margin-bottom: 1.25rem;
	img {
		height: 3.3rem;
		width: auto;
		@include media-breakpoint-up(lg) {
			height: 2.9rem;
		}
	}
}