.bg-header-dark {
  background-color: $bg-header-dark;
}
.bg-header-xl-dark {
  @include media-breakpoint-up(xl){
    background-color: $bg-header-dark !important;
  }
}
.bg-menu {
  background: $menu-color;
  @include media-breakpoint-down(lg){
    border-bottom: 1px solid white;
  }
}
@include media-breakpoint-down(lg){
  .header-fixed-mobile {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: $zindex-fixed;
    height:4.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .header-fixed-mobile-placeholder{
    padding-top:4.5rem;
  }
}

.border-bottom-menu-color{
  border-bottom: 3px solid $menu-color;
  @include media-breakpoint-up(xxl){
    border-bottom-width: 3px;
  }
}

.header-carousel{
  .carousel-item{
    background-color: $primary;
    background-repeat: no-repeat;
    background-position: right center;
    background-size: cover;
    .carousel-item-bg{
      min-height: 30rem;
      background: linear-gradient(110deg, $primary 0%, $primary 20%, rgba($primary, .5) 55%, rgba($primary, 0.1) 70%, rgba($primary, 0) 90%, rgba($primary, 0) 100%);
    }

    @include media-breakpoint-up(lg) {
      .carousel-item-bg {
        min-height: 30rem;
        background: linear-gradient(110deg, $primary 0%, $primary 32%, rgba($primary, .5) 48%, rgba($primary, 0.1) 60%, rgba($primary, 0) 70%, rgba($primary, 0) 100%);
      }
    }
    @include media-breakpoint-up(xl) {
      .carousel-item-bg {
        min-height: 24rem;
      }
    }
    @include media-breakpoint-up(xxl) {
      background-size: 65% auto;

      .carousel-item-bg {
        min-height: 30rem;
        background: linear-gradient(110deg, $primary 0%, $primary 42%, rgba($primary, .5) 58%, rgba($primary, 0.1) 70%, rgba($primary, 0) 80%, rgba($primary, 0) 100%);
      }
    }

    @media (min-width:2800px) {
      background-size: 55% auto;

      .carousel-item-bg {
        min-height: 35rem;
        background: linear-gradient(110deg, $primary 0%, $primary 55%, rgba($primary, .5) 63%, rgba($primary, 0.1) 75%, rgba($primary, 0) 85%, rgba($primary, 0) 100%);
      }
    }

  }

  .header-carousel-nav{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width:100%;
    z-index: 10;

    .nav-item{
      margin-left: 2px;
    }
    .nav-link {
      padding: 0.5*$spacer $spacer;
      @include media-breakpoint-up(xxl){
        padding: $spacer;
      }

      color: white;
      @extend .font-weight-bold;
      font-size: $font-size-base;
    }
  }

  .header-carousel-bottom{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width:100%;
    z-index: 10;
    height:1px;

    .container{
      position: relative;

      .nav-faculties--small{
        position: absolute;
        right: $grid-gutter-width/2;
        bottom: -1px;
      }
    }
  }

  .header-carousel-heading {
    text-transform: uppercase;
    > a, > span ,
    > div > a, > div > span {
      display: inline;
      background: linear-gradient(0deg, rgba($bg-header-dark,0) 0, rgba($bg-header-dark,0) 5%, $bg-header-dark 5%, $bg-header-dark 98%, rgba($bg-header-dark,0) 98%, rgba($bg-header-dark,0) 100%);
      font-weight: normal;
      box-decoration-break: clone;
      box-sizing: border-box;
      margin: 0;
      padding: 0 $spacer;
      text-decoration: none !important;
      color: white !important;
      border-top:1px solid transparent;
      border-bottom:1px solid transparent;

      font-size: 2.6rem;
      line-height: 3.4rem;

      &.small {
        font-size: 1.6rem !important;
        line-height: 2.2rem;
      }

      @include media-breakpoint-up(lg){
        font-size: 3.4rem;
        line-height: 4.5rem;

        &.small {
          font-size: 2rem !important;
          line-height: 2.55rem;
        }
      }
    }
    >div {
      position: relative;
      height: auto;
      & + div {
        margin-top: 3px;

        @include media-breakpoint-up(lg) {
          margin-top: 5px;
        }
      }
    }
  }

  .header-carousel-btn {
    @extend .btn;
    @extend .btn-primary;
    @extend .rounded-0;
    padding: .5*$spacer $spacer;
    background: $bg-header-dark;
    &:hover, &:focus, &:active {
      background: mix($bg-header-dark,white,90%);
      border-color: mix($bg-header-dark,white,90%);
    }
    display: none;
    @include media-breakpoint-up(lg){
      display:inline-block;
    }
  }

  .header-carousel-indicators {
    z-index: 15;
    padding-left: 0; // override <ol> default
    list-style: none;
    display: flex;
    flex-direction: row;
    margin: $spacer auto $spacer 0;

    li {
      position: relative;
      flex: 0 1 auto;
      width: 10px;
      height: 10px;
      margin-right: 5px;
      margin-left: 5px;
      text-indent: -999px;
      cursor: pointer;
      background-color: rgba($carousel-indicator-active-bg, .5);

      // Use pseudo classes to increase the hit area by 10px on top and bottom.
      &::before {
        position: absolute;
        top: -10px;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 10px;
        content: "";
      }
      &::after {
        position: absolute;
        bottom: -10px;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 10px;
        content: "";
      }
    }

    .active {
      background-color: $carousel-indicator-active-bg;
    }
  }
}
