.new-articles {
	position: relative;
	background: $body-bg;


	.article {
		display: flex;
		flex-direction: column;
		align-items: stretch;
		padding-right: ($grid-gutter-width / 4);
		padding-left: ($grid-gutter-width / 4);
		margin-bottom: ($grid-gutter-width / 2);
		width: 100%;
		position:relative;

		@include media-breakpoint-up(md){
			flex:0 0 33.33333%;
			max-width:33.33333%;
		}
		@include media-breakpoint-up(xl){
			flex:0 0 25%;
			max-width:25%;
		}

		.content-col-9 &{
			@include media-breakpoint-up(md){
				flex:0 0 50%;
				max-width:50%;
			}
			@include media-breakpoint-up(xl){
				flex:0 0 33.33333%;
				max-width:33.33333%;
			}
		}
		.content-col-6 &{
			@include media-breakpoint-up(md){
				flex:0 0 50%;
				max-width:50%;
			}
		}
		.content-col-3 &{
			flex:0 0 100%;
			max-width:100%;
			margin-left: 0;
		}

		&.article-pinned:after{
			display: block;
			font-family: 'icons';
			font-size: 90%;
			background: $bg-header-dark;
			color: white;
			content: $icon-pin;
			position: absolute;
			left: 2.2rem;
			top: -0.25rem;
			padding: .2rem .2rem .1rem .2rem;
		}
	}
	.article-image {
		display: block;
		border: none;
		background: $light;
		width: 100%;
		margin-bottom: .5rem;
		overflow: hidden;
		transition: box-shadow 0.1s ease-in-out;
		box-shadow: 0 .25rem 0 0 $primary;
		position: relative;

		&:hover,
		&:focus {
			box-shadow: 0 0 0 .5rem $primary;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@include media-breakpoint-down(sm){
			max-height: 20rem;
		}

		@include media-breakpoint-up(md) {
			height: 11rem;
		}
		@include media-breakpoint-up(lg) {
			height: 11rem;
		}
		@include media-breakpoint-up(xxl) {
			height: 13rem;
		}
	}
	.article-date {
		@extend .small;
		@extend .text-muted;
		padding-top: .25*$spacer;
	}
	.article-title {
		font-size: 1.25rem;
		line-height: 1.2;
		margin-top: 0 !important;
		margin-bottom: $headings-margin-bottom !important;
		@include media-breakpoint-down(sm) {
			font-size: $font-size-base;
		}
		a {
			color: $body-color;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
	}
}