@mixin heading-decoration-align($decorationWidth){
  $decorationHalfWidth:$decorationWidth*.5;

  &:after{
    width: $decorationWidth;
  }

  &.text-center{
    &:after {
      left: calc(50% - #{$decorationHalfWidth});
    }
  }
  &.text-right{
    &:after {
      left: auto;
      right:0;
    }
  }
  @include media-breakpoint-up(lg){
    &.text-lg-center {
      &:after {
        left: calc(50% - #{$decorationHalfWidth});
      }
    }
    &.text-lg-left {
      &:after {
        left: 0;
      }
    }
    &.text-lg-right {
      &:after {
        right:0;
        left:auto;
      }
    }
  }
  @include media-breakpoint-up(xl){
    &.text-xl-center {
      &:after {
        left: calc(50% - #{$decorationHalfWidth});
      }
    }
    &.text-xl-left {
      &:after {
        left: 0;
      }
    }
    &.text-xl-right {
      &:after {
        right:0;
        left:auto;
      }
    }
  }
  @include media-breakpoint-up(xxl){
    &.text-xxl-center {
      &:after {
        left: calc(50% - #{$decorationHalfWidth});
      }
    }
    &.text-xxl-left {
      &:after {
        left: 0;
      }
    }
    &.text-xxl-right {
      &:after {
        right:0;
        left:auto;
      }
    }
  }
}

h1, .h1,
h2, .h2,
h3, .h3 {
  color: $bg-header-dark;
  position: relative;
  padding-bottom: .35rem;

  &:after{
    display: block;
    position: absolute;
    content: '';
    background: $primary;
    bottom: 0;
    left: 0;
    height: .25rem;
  }

  @include heading-decoration-align(3.5rem);
}
h2,.h2 {
  @include heading-decoration-align(3rem);
}
h3,.h3 {
  @include heading-decoration-align(2.5rem);
}
h4, .h4,
h5, .h5,
h6, .h6{
  color: $bg-header-dark;
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
  a &,
  & a {
    color: inherit;
  }
  &:after{
    display: none;
  }
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6{
  &.text-primary{
    color: $primary;
  }
  &.text-secondary{
    color: $secondary;
  }
  &.text-white{
    color: $white;
  }
  &.text-danger{
    color: $danger;
  }
}

h1, .h1,
h2, .h2,
h3, .h3{
  &.text-primary,
  &.text-secondary,
  &.text-white,
  &.text-danger,
  a &,
  &.icon,
  &:has(a){
    padding: 0;
    &:after{
      display: none !important;
    }
  }
  a &,
  &:has(a:not(.btn)) {
    color: inherit !important;
  }
  &.heading-decoration-danger{
    &:after{
      background: $danger;
    }
  }
  &.heading-decoration,
  &.heading-decoration-dark,
  &.heading-decoration-white{
    &:after{
      display: block !important;
    }
  }
  &.heading-decoration-dark{
    &:after{
      background: $bg-header-dark;
    }
  }
  &.heading-decoration-white{
    &:after{
      background: white;
    }
  }
  &.text-decoration-none,
  &.heading-decoration-none {
    &:after{
      display: none !important;
    }
  }
}

h1, .h1 {
  margin-top: 1.5*$spacer;
  margin-bottom: 1.5*$spacer;
}
h2, .h2{
  margin-top: 1.1*$spacer;
  margin-bottom: 0.8*$spacer;
}
h3, .h3{
  margin-top: $spacer;
  margin-bottom: 0.5*$spacer;
}

.text-primary,
.text-secondary,
.text-white,
.text-danger {
  h1, h2, h3, h4, h5, h6,
  .h1, .h2, .h3, .h4, .h5, .h6{
    color: inherit;
  }
  h1, h2, h3,
  .h1, .h2, .h3{
    padding: 0;
    &:after{
      display: none !important;
    }
  }
}

.size-h1,.size-h2,.size-h3,.size-h4,.size-h5,.size-h6 {
  font-weight: $headings-font-weight;
}

.size-h1 {
  font-size: $h1-font-size;
}
.size-h2 {
  font-size: $h2-font-size;
}
.size-h3 {
  font-size: $h3-font-size;
}
.size-h4 {
  font-size: $h4-font-size;
}
.size-h5 {
  font-size: $h5-font-size;
}
.size-h6 {
  font-size: $h6-font-size;
}

@include media-breakpoint-down(md) {
  h1, .h1, .size-h1 { font-size: $h1-font-size-sm; }
  h2, .h2, .size-h2 { font-size: $h2-font-size-sm; }
  h3, .h3, .size-h3 { font-size: $h3-font-size-sm; }
  h4, .h4, .size-h4 { font-size: $h4-font-size-sm; }
  h5, .h5, .size-h5 { font-size: $h5-font-size-sm; }
  h6, .h6, .size-h6 { font-size: $h6-font-size-sm; }
}