// Font sizes
.display-0 {
	font-size: $display0-size;
	font-weight: $font-weight-normal;
}
.display-1,
.display-2,
.display-3,
.display-4,
.display-5 {
	font-weight: $font-weight-normal;
}
@include media-breakpoint-down(md) {
	.lead { font-size: $h6-font-size-sm; }
	.display-0 { font-size: $display0-size-sm; }
	.display-1 { font-size: $display1-size-sm; }
	.display-2 { font-size: $display2-size-sm; }
	.display-3 { font-size: $display3-size-sm; }
	.display-4 { font-size: $display4-size-sm; }
}


// Blockquotes
.alertblock,
.blockquote {
	color: $body-bg;
	background: $primary;
	font-size: $blockquote-font-size;
	box-sizing: border-box;
	border: 1px solid $primary;

	&.bg-none {
		border-color: transparent;
	}

	.alertblock-content,
	.blockquote-content {
		margin: ($spacer * 1.5) ($spacer * 2);
		@include media-breakpoint-up(lg) {
			margin: ($spacer * 2.5) ($spacer * 3);
		}
	}

	a,
	a:hover{
		color: inherit;
	}

	h1, h2, h3 {
		color: inherit;
		&:after {
			display: none !important;
		}
	}

}
.alertblock {
    .alertblock-icon {
        opacity: .5;
        font-size: $h1-font-size*1.2;
        margin-bottom: $spacer*.2;
		margin-top: -.5*$spacer;
        @include media-breakpoint-down(md){
          font-size: $h1-font-size-sm*1.2;
          margin-bottom: $spacer*.15;
        }
    }
}

.blockquote {
	font-style: italic;
	margin-bottom: $spacer;
	line-height: 1.8;

	&:not(.text-center){
		.blockquote-content::before {
			font-style: normal;
			font-family: sans-serif;
			content: '\201d';
			display: block;
			font-size: $font-size-base * 7.5;
			line-height: 1;
			margin-bottom: -($font-size-base * 4);
			opacity: .5;
		}
	}
}

.blockquote-footer {
	font-style: normal;
	display: block;
	font-size: inherit;
	margin-top: $spacer;
	font-weight: $font-weight-bold;
	line-height: $line-height-base;
	color: $blockquote-small-color;

	&::before {
		content: '';
	}
}

.hover-reverse {
	text-decoration: $link-hover-decoration;
	&:hover {
		text-decoration: $link-decoration;
	}
}



.number-list {
	padding: 0;
	list-style-type: none;
	&>li {
		display: flex;
		margin: 0 0 3rem;
		&:not([class*=col-]) {
			padding: 0;
		}
	}
	.content *:last-child{
		margin-bottom: 0;
		padding-bottom: 0;
	}
	.list-number {
		font-size: 6rem;
		line-height: .8;
		font-weight: $font-weight-light;
		color: $primary;
		min-width: 4rem;
		@include media-breakpoint-up(md) {
			margin-right: $spacer*.5;
		}
		@include media-breakpoint-up(xl) {
			margin-left: $spacer*.5;
		}
	}
}