.breadcrumb {
	display: flex;
	flex-wrap: wrap;
	padding: $breadcrumb-padding-y $breadcrumb-padding-x;
	list-style: none;
	background-color: $breadcrumb-bg;
	@include border-radius($breadcrumb-border-radius);
}

.breadcrumb {
	border-radius: 0;
	padding: 0 0 0 10px;
	margin: 1rem 0;
}
.breadcrumb-item {
	border: none;
	box-sizing: border-box;
	margin: 0 0 1px -10px;
	max-width: 100%;

	a,
	> span {
		text-decoration: none;
		background: #e0e0e0;
		background: linear-gradient(0deg, white 0, white 8%, #e0e0e0 5%, #e0e0e0 92%, white 92%, white 100%);
		position: relative;
		box-sizing: border-box;

		&, > span {
			display: block;
			text-wrap: nowrap;
			text-overflow: ellipsis;
			max-width: 100%;
			overflow: hidden;
			clip-path: polygon(0 0, 100% 0, calc(100% - 10px) 100%, 0 100%);
		}

		>span {
			padding: 4px 18px 4px 14px;
			background: white;
		}
	}

	a:hover > span{
		text-decoration: underline !important;
	}

	&.web-hierarchy {
		a {
			background: white;
		}
		a >span {
			background: $link-color;
			color: white;
		}
		a:hover > span{
			background: $link-hover-color;
			text-decoration: none !important;
		}

		+ .breadcrumb-item {
			a, > span {
				background: white !important;
			}
		}
	}

	& + .breadcrumb-item {
		a,
		>span {
			padding-left: 1px;

			&, >span{
				clip-path: polygon(10px 0, 100% 0, calc(100% - 10px) 100%, 0 100%);
			}

			> span {
				padding: 4px 18px;
			}
		}
	}
}